html {
  font-size: 100%;
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  height: var(--app-height);
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  min-width: 16.5625rem;
  min-height: 40.625rem;
  height: 100vh;
  height: var(--app-height);
  font-family: "Josefin Sans", sans-serif;
  background: #e9795d;
  color: #003049;
  letter-spacing: -0.0625rem;
}
@media (min-width: 48em) {
  body {
    min-height: 56.25rem;
  }
}

ul {
  list-style: none;
}

img {
  width: 100%;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --app-height: 100%;
}

.container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  width: 95%;
  max-width: 21.25rem;
  min-width: 16.5625rem;
  height: 90%;
  min-height: 37.5rem;
  max-height: 46.875rem;
  margin: 0 auto;
  background-color: #fff9e7;
  border-radius: 2.8125rem;
  border: solid #f9c74f 0.625rem;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
@media (min-width: 48em) {
  .container {
    padding: 1.25rem 1.875rem;
    max-width: 46.875rem;
    max-height: 59.375rem;
    min-height: 50rem;
    display: grid;
    grid-template-rows: 1fr 6fr;
    grid-template-columns: 1fr;
    justify-items: center;
    align-items: stretch;
  }
}
@media (min-width: 64em) {
  .container {
    max-width: 68.75rem;
  }
}

.hidden {
  display: none !important;
}
@media (min-width: 48em) {
  .hidden {
    display: block !important;
  }
}

.show {
  display: block !important;
}

@media (min-width: 48em) {
  main {
    display: grid;
    grid-template-columns: 5fr 6fr;
    justify-content: center;
    justify-items: center;
    margin-bottom: 9.375rem;
  }
}
@media (min-width: 64em) {
  main {
    width: 100%;
    margin-bottom: 2.5rem;
  }
}

.btn {
  font-family: "Croissant One", cursive;
  letter-spacing: -0.125rem;
  color: #003049;
  border-radius: 1.875rem;
  background-color: #f4c8a6;
  border: solid 1px #e75c3a;
  box-shadow: -3px 4px 0px #e75c3a;
  transition: all 100ms linear;
  cursor: pointer;
}
.btn:hover {
  transform: translate(-3px, 4px);
  box-shadow: none;
}
.btn--start {
  margin-top: 2.25rem;
  font-size: 1.5625rem;
  padding: 0.3125rem 3.25rem;
}
@media (min-width: 48em) {
  .btn--start {
    display: none;
  }
}
.btn--search {
  color: #e75c3a;
  font-size: 1rem;
  padding: 0.5rem 1.5rem 0.5rem 1.375rem;
}
@media (min-width: 64em) {
  .btn--search {
    box-shadow: none;
    border-radius: 1.875rem;
    border: none;
    border-left: solid 1px #e75c3a;
  }
  .btn--search:hover {
    transform: none;
    background-color: #faba8a;
  }
}
.btn--nav {
  color: #e75c3a;
  font-size: 2.5rem;
  width: 5rem;
  height: 5rem;
  border-radius: 100%;
}
@media (min-width: 64em) {
  .btn--nav {
    width: auto;
    height: 2.5rem;
    font-size: 1rem;
    border-radius: 1.875rem;
    padding: 0 0.9375rem;
  }
}

.header {
  max-width: 62.5rem;
}
@media (min-width: 48em) {
  .header {
    display: flex;
    align-items: center;
    gap: 3.4375rem;
  }
}
@media (min-width: 64em) {
  .header {
    justify-content: space-between;
    width: 100%;
    gap: 0;
  }
}
.header__search {
  width: 0;
  height: 0;
  opacity: 0;
  transition: all 500ms ease-in-out;
}
.header__search--mobile {
  width: auto;
  height: auto;
  opacity: 1;
}
@media (min-width: 48em) {
  .header__search {
    width: auto;
    height: auto;
    opacity: 1;
    display: flex;
    gap: 0.625rem;
  }
}
@media (min-width: 64em) {
  .header__search {
    gap: 0;
    justify-items: center;
    background-color: #fff;
    border-radius: 1.875rem;
    border: solid 1px #e75c3a;
    width: auto;
    max-width: 21.25rem;
    box-shadow: -3px 4px 0px #e75c3a;
    line-height: 1.125rem;
    overflow: hidden;
    padding-left: 0.75rem;
    font-family: "Josefin Sans", sans-serif;
    font-size: 1rem;
    color: #003049;
    letter-spacing: -0.0625rem;
    transition: all 100ms ease-in-out;
    -webkit-appearance: none;
  }
  .header__search:hover {
    transform: translate(-3px, 4px);
    box-shadow: none;
  }
}

.title {
  display: flex;
  gap: 0.3125rem;
  justify-content: center;
  align-items: center;
  transition: all 500ms ease-in-out;
}
.title--mobile {
  transform: translateY(-10px);
}
@media (min-width: 48em) {
  .title--mobile {
    transform: none;
  }
}
.title__logo {
  width: 2.8125rem;
  height: 2.8125rem;
}
@media (min-width: 48em) {
  .title__logo {
    width: 3.4375rem;
    height: 3.4375rem;
  }
}
@media (min-width: 64em) {
  .title__logo {
    width: 2.1875rem;
    height: 2.1875rem;
  }
}
.title__text {
  font-family: "Croissant One", cursive;
  font-size: 2.375rem;
  letter-spacing: -0.25rem;
}
@media (min-width: 48em) {
  .title__text {
    font-size: 3.125rem;
  }
}
@media (min-width: 64em) {
  .title__text {
    font-size: 2.5rem;
  }
}

.nav {
  display: none;
}
@media (min-width: 48em) {
  .nav {
    display: block;
    position: absolute;
    bottom: 2.8125rem;
    left: 50%;
    transform: translate(-50%);
  }
  .nav__list {
    display: flex;
    gap: 5rem;
  }
  .nav__title {
    display: none;
  }
}
@media (min-width: 64em) {
  .nav {
    position: static;
    transform: none;
  }
  .nav__list {
    gap: 0.625rem;
  }
  .nav__title {
    display: inline-block;
    margin-left: 0.3125rem;
  }
}

.bookmarks .message {
  display: none;
}

.recipe {
  margin-top: 2.25rem;
}
@media (min-width: 48em) {
  .recipe {
    margin-top: 0;
  }
}

.results {
  display: none;
  transition: all 500ms ease-in-out;
  background-color: #fff;
  width: 92.5%;
  border-radius: 1.875rem;
  margin: 1.25rem auto;
  margin-bottom: 0;
  height: 60vh;
  min-height: 25.5rem;
  max-height: 33.125rem;
  border: solid 2px #e75c3a;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}
@media (min-width: 48em) {
  .results {
    display: block;
    opacity: 1;
    background-color: #fff;
    border-radius: 1.875rem;
    height: 100%;
    margin: 0 auto;
    max-height: none;
    border: solid 2px #e75c3a;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  }
}
@media (min-width: 64em) {
  .results {
    margin-top: 0.625rem;
    width: 90%;
    max-height: none;
  }
}
.results .message {
  text-align: center;
  padding: 0.625rem;
}
.results .message__title {
  margin-top: 3.75rem;
  font-size: 2.5rem;
  font-weight: 500;
  letter-spacing: -0.125rem;
}
.results .message .animation {
  margin: -2.5rem auto 0;
}

.search {
  transition: all 500ms ease-in-out;
}
.search__field {
  background-color: #fff;
  border-radius: 1.875rem;
  border: solid 1px #e75c3a;
  width: 50vw;
  min-width: 8.75rem;
  max-width: 12.1875rem;
  margin-right: 0.5rem;
  box-shadow: -3px 4px 0px #e75c3a;
  line-height: 1.125rem;
  padding: 0.75rem 1rem 0.5rem 1rem;
  font-family: "Josefin Sans", sans-serif;
  font-size: 1rem;
  color: #003049;
  letter-spacing: -0.0625rem;
  transition: all 100ms ease-in-out;
  -webkit-appearance: none;
}
@media (min-width: 48em) {
  .search__field {
    max-width: none;
    width: 13.125rem;
  }
}
@media (min-width: 64em) {
  .search__field {
    background-color: none;
    border-radius: 0;
    border: none;
    width: 18vw;
    max-width: 18.75rem;
    box-shadow: none;
    line-height: 1.125rem;
    padding: 0;
    font-family: "Josefin Sans", sans-serif;
    font-size: 1rem;
    color: #003049;
    letter-spacing: -0.0625rem;
    transition: all 100ms ease-in-out;
    -webkit-appearance: none;
  }
}
.search__field:focus {
  transform: translate(-3px, 4px);
  box-shadow: none;
  border: solid 2px #f9c74f;
  outline: none;
  overflow: visible;
}
.search__field:focus::placeholder {
  visibility: hidden;
}
@media (min-width: 64em) {
  .search__field:focus {
    border: none;
    outline: none;
    transform: none;
  }
}
.search__field::placeholder {
  color: #003049;
  line-height: 1.125rem;
  text-overflow: ellipsis;
  opacity: 0.5;
}
.search__text {
  display: none;
}

.welcome {
  text-align: center;
}
@media (min-width: 48em) {
  .welcome {
    margin-top: 4.375rem;
  }
}
.welcome__title {
  font-size: 1.75rem;
}
@media (min-width: 48em) {
  .welcome__title {
    font-size: 2.5rem;
  }
}
@media (min-width: 64em) {
  .welcome__title {
    margin-top: 5.625rem;
  }
}
.welcome__text {
  margin-top: 0.9375rem;
  font-size: 1.125rem;
  width: 18ch;
}
@media (min-width: 48em) {
  .welcome__text {
    font-size: 1.25rem;
  }
}
.welcome__subtitle {
  font-size: 1.375rem;
  margin-top: 1.75rem;
}
@media (min-width: 48em) {
  .welcome__subtitle {
    font-size: 1.5625rem;
  }
}
.welcome__features {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.features__item {
  display: flex;
  align-items: center;
  margin-top: 1rem;
  gap: 1.5625rem;
}
@media (min-width: 48em) {
  .features__item {
    margin-top: 1.375rem;
  }
}
.features__icon {
  color: #e75c3a;
  font-size: 1.375rem;
}
@media (min-width: 48em) {
  .features__icon {
    font-size: 1.625rem;
  }
}
.features__text {
  width: 15ch;
  text-align: left;
}
@media (min-width: 48em) {
  .features__text {
    font-size: 1.125rem;
  }
}